:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.gallery {
  max-width: 1140px;
  grid-gap: 24px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 240px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.gallery__item {
  height: 150px;
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery__image:hover {
  transform: scale(1.03);
}

.gallery__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery__link {
  height: 100%;
  text-decoration: none;
  display: block;
}

.gallery__link:hover {
  cursor: zoom-in;
}

.info {
  color: #fff;
  width: 100%;
  background-color: rgba(0, 0, 0, .4);
  align-items: center;
  gap: 24px;
  margin: 0;
  padding: 10px;
  display: flex;
}

.info-item {
  flex-direction: column;
  flex-grow: 1;
  align-items: center;
  display: flex;
}

.search-form {
  justify-content: center;
  align-items: center;
  padding-top: 30px;
  display: flex;
}

input[type="text"], button {
  height: 40px;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 0 10px;
}

.search-form form {
  text-align: center;
}

.search-form input, .search-form button {
  border: none;
  outline: none;
}

.container {
  z-index: 1;
  width: 100%;
  height: 80px;
  background-color: #00f;
  position: fixed;
  left: 50%;
  transform: translate(-50%, -50%);
}

.is-hidden {
  display: none;
}

.load-more {
  width: 100%;
  background-color: #a5f5da;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
}

